.search {
  width: 100%;
  position: relative;
  display: flex;
}

.searchTerm {
  width: 100%;
  border: 3px solid #00b4cc;
  padding: 5px;
  height: 30px;
  border-radius: 5px;
  outline: none;
  color: #9dbfaf;
}

.searchTerm:focus {
  color: #00444d;
}

/*Resize the wrap to see the search bar change!*/
.wrap {
  width: 30%;
  margin-top: 1%;
  padding-bottom: 2%;
  position: relative;
  left: 50%;
  transform: translate(-50%, -20%);
}
